import React from 'react';
import { graphql } from 'gatsby';
import Img from 'gatsby-image';
import { Query } from '../../graphql-types';
import { Layout } from '../components/layout';
import { SEO } from '../components/seo';
import { Separator } from '../components/separator';
import { BreadCrumbs } from '../components/breadcrumbs';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { FormattedMessage, Link, useIntl } from 'gatsby-plugin-intl';

const AboutPage: React.FunctionComponent<{ data: Query }> = ({ data }) => {
  const intl = useIntl();
  const { allFile } = data;
  const firm = allFile.edges.find((v) => v.node.name === 'firm').node.childImageSharp.fluid;
  const services = allFile.edges.find((v) => v.node.name === 'services').node.childImageSharp.fluid;
  const commitment = allFile.edges.find((v) => v.node.name === 'commitment').node.childImageSharp.fluid;

  function renderContactUs() {
    return (
      <p>
        <Link className="btn btn-primary" to="/contact">
          <strong>
            <FormattedMessage id="contact-us-today" />
          </strong>
        </Link>
      </p>
    );
  }
  function renderContent(locale: string) {
    switch (locale) {
      case 'fr':
        return (
          <Container>
            <Row>
              <Separator />
              <Col>
                <Img fluid={firm} className="img-fluid" alt="Firm" />
              </Col>
              <Col>
                <h1>FOURNISSEURS DE SERVICES COMPLETS POUR ÉQUIPEMENTS AÉROPORTUAIRES (GSE)</h1>
                <p className="text-justify">
                  Aviaco GSE est un spécialiste des équipements aéroportuaires (GSE) pour le secteur de l’aviation. Travailler avec nous
                  signifie travailler avec une équipe spécifique de professionnels. Des experts qui vous offrent tous les services dont vous
                  avez besoin pour assurer le bon fonctionnement de votre aéroport. Si vous avez besoin d'acheter ou de louer du nouveau
                  matériel ou du matériel d'occasion, d'aider à la gestion de flotte, à l'entretien ou aux réparations, ou que vous
                  souhaitiez simplement un petit aperçu et des conseils amicaux, Aviaco est toujours à votre service. Contactez-nous
                  aujourd'hui !
                </p>
              </Col>
            </Row>

            <hr />

            <Row>
              <Col>
                <Img fluid={services} className="img-fluid" alt="Services" />
              </Col>
              <Col>
                <h2>EXPÉRIENCE AVEC GSE</h2>
                <p className="text-justify">
                  Aviaco est construit sur l'expérience. L'entreprise s'est développée pour devenir un fournisseur de services complets dans
                  le domaine des équipements aéroportuaires (GSE). Vous trouverez des nouveaux GSE et des GSE d'occasion à vendre sur notre
                  site Web, mais vous pouvez également faire appel à nous pour louer GSE à court et long terme, compter sur nos services de
                  gestion de flotte et même faire entretenir et réparer votre GSE par nos techniciens qualifiés.
                </p>
                <h3>NOS VALEURS</h3>
                <p className="text-justify">
                  Chez Aviaco, nous créons et entretenons des relations commerciales ouvertes et honnêtes avec nos clients. Nous adoptons
                  une approche à livre ouvert de la qualité du service et des prix. En travaillant avec Aviaco, vous bénéficiez de:
                  <ul>
                    <li>
                      <strong>Transparence</strong>
                      <br />
                      Pas de frais cachés.
                    </li>
                    <li>
                      <strong>Adéquation</strong>
                      <br />
                      Nous vous conseillons et vous soumettons les équipements qui répondent le mieux à vos besoins.
                    </li>
                    <li>
                      <strong>Empathie</strong>
                      <br />
                      Nous regardons les choses de votre point de vue, en fournissant des conseils, un soutien et des commentaires ciblés.
                    </li>
                    <li>
                      <strong>Facilitation</strong>
                      <br />
                      Vos besoins sont satisfaits grâce à nos services à valeur ajoutée tels que le transport, la mise en service et la
                      maintenance sur site.
                    </li>
                    <li>
                      <strong>Réalisation</strong>
                      <br />
                      Vos besoins et vos attentes sont notre priorité.
                    </li>
                  </ul>
                </p>
              </Col>
            </Row>

            <hr />

            <Row>
              <Col>
                <Img fluid={commitment} className="img-fluid" alt="Commitment" />
              </Col>
              <Col>
                <h3>NOUS CONTACTER</h3>
                <p className="text-justify">
                  Souhaitez-vous discuter vos besoins de GSE avec nous?
                  <br />
                  Vous recherchez un équipement GSE en particulier?
                  <br />
                  Nous sommes impatients de vous aider.
                </p>
                {renderContactUs()}
              </Col>
            </Row>
          </Container>
        );
      case 'es':
        return (
          <Container>
            <Row>
              <Separator />
              <Col>
                <Img fluid={firm} className="img-fluid" alt="Firm" />
              </Col>
              <Col>
                <h1>PROVEEDOR DE SERVICIOS INTEGRALES - GSE</h1>
                <p className="text-justify">
                  Aviaco GSE es especialista en Equipos de Apoyo en Tierra (GSE) para el sector de la aviación. Trabajar con nosotros es
                  trabajar con un selecto equipo de profesionales. Expertos que le ofrecen todos los servicios que necesita para que su
                  aeropuerto funcione sin problemas. Tanto si necesita comprar o alquilar equipos nuevos o de segunda mano, si necesita
                  ayuda con la gestión de su flota, o con el mantenimiento o las reparaciones, o simplemente si desea obtener algo de
                  información o consejos cordiales, Aviaco está siempre a su servicio. ¡Póngase en contacto con nosotros ya!
                </p>
              </Col>
            </Row>

            <hr />

            <Row>
              <Col>
                <Img fluid={services} className="img-fluid" alt="Services" />
              </Col>
              <Col>
                <h2>EXPERIENCIA CON GSE</h2>
                <p className="text-justify">
                  Aviaco se basa en la experiencia. La empresa ha crecido hasta convertirse en un proveedor de servicios integrales en el
                  campo de Equipos de Apoyo en Tierra (GSE). No solo encontrará GSE nuevos y de segunda mano a la venta en nuestro sitio
                  web, sino que también puede llamarnos para alquiler GSE a corto y largo plazo, confiar en nuestros servicios de gestión de
                  flotas e incluso mantener sus equipos revisados y reparados por nuestros técnicos cualificados.
                </p>
                <h3>NUESTROS VALORES</h3>
                <p className="text-justify">
                  En Aviaco, creamos y fomentamos relaciones comerciales abiertas y honestas con nuestros clientes, donde la confianza es
                  esencial, por tanto estamos fuertemente comprometidos a mantener siempre una política de "libros abiertos" en términos de
                  calidad de servicio y fijación de precios. Trabajando con Aviaco, se beneficia de:
                  <ul>
                    <li>
                      <strong>Transparencia</strong>
                      <br />
                      Sin costes ocultos en nuestras ofertas.
                    </li>
                    <li>
                      <strong>Idoneidad</strong>
                      <br />
                      Ofertando siempre los tipos de equipos que satisfagan mejor las necesidades de nuestros clientes.
                    </li>
                    <li>
                      <strong>Empatía</strong>
                      <br />
                      Miramos las cosas desde su perspectiva, brindando asesoramiento, apoyo y sugerencias específicos.
                    </li>
                    <li>
                      <strong>Facilitación</strong>
                      <br />
                      Cubrimos sus necesidades a través de nuestros servicios de valor añadido, como el transporte, la puesta en servicio in
                      situ y el mantenimiento.
                    </li>
                    <li>
                      <strong>Cumplimiento</strong>
                      <br />
                      Sus necesidades y expectativas son nuestro objetivo.
                    </li>
                  </ul>
                </p>
              </Col>
            </Row>

            <hr />

            <Row>
              <Col>
                <Img fluid={commitment} className="img-fluid" alt="Commitment" />
              </Col>
              <Col>
                <h3>CONTACTENOS</h3>
                <p className="text-justify">
                  Desea comentar sus necesidades de GSE con nosotros?
                  <br />
                  Está buscando un determinado equipo?
                  <br />
                  Estamos deseando ayudarle.
                </p>
                {renderContactUs()}
              </Col>
            </Row>
          </Container>
        );
      default:
        return (
          <Container>
            <Row>
              <Separator />
              <Col>
                <Img fluid={firm} className="img-fluid" alt="Firm" />
              </Col>
              <Col>
                <h1>Full-service providers for GSE</h1>
                <p className="text-justify">
                  Aviaco GSE is a specialist in Ground Support Equipment (GSE) for the aviation sector. Working with us means working with a
                  select team of professionals. Experts who offer you every service you need to keep your airport running smoothly. Whether
                  you need to buy or rent new or second-hand equipment, help with fleet management, maintenance or repairs, or would simply
                  like a little insight and friendly advice, Aviaco is always at your service. Contact us today!
                </p>
              </Col>
            </Row>

            <hr />

            <Row>
              <Col>
                <Img fluid={services} className="img-fluid" alt="Services" />
              </Col>
              <Col>
                <h2>Experience with GSE</h2>
                <p className="text-justify">
                  Aviaco is built on experience. The company has grown to become a full-service provider in the field of Ground Support
                  Equipment (GSE). You’ll not only find new and second-hand GSE for sale on our website, you’re also able to call on us to
                  lease GSE for the short and long term, rely on our fleet management services and even have your GSE maintained and
                  repaired by our qualified technicians.
                </p>
                <h3>Our values</h3>
                <p className="text-justify">
                  At Aviaco, we create and nurture open, honest business relationships with our customers. We take an open-book approach to
                  service quality and pricing. Working with Aviaco, you benefit from:
                  <ul>
                    <li>
                      <strong>Transparency</strong>
                      <br />
                      No hidden costs.
                    </li>
                    <li>
                      <strong>Suitability</strong>
                      <br />
                      We advise and provide quotes on the equipment that best meets your needs.
                    </li>
                    <li>
                      <strong>Empathy</strong>
                      <br />
                      We look at things from your perspective, providing advice, support and targeted feedback.
                    </li>
                    <li>
                      <strong>Facilitation</strong>
                      <br />
                      Your needs are met through our value-added services such as transport, on-site commissioning and maintenance.
                    </li>
                    <li>
                      <strong>Fulfillment</strong>
                      <br />
                      Your needs and expectations are our focus.
                    </li>
                  </ul>
                </p>
              </Col>
            </Row>

            <hr />

            <Row>
              <Col>
                <Img fluid={commitment} className="img-fluid" alt="Commitment" />
              </Col>
              <Col>
                <h3>Contact us</h3>
                <p className="text-justify">
                  Would you like to discuss your GSE needs with us?
                  <br />
                  Are you looking for a particular piece of GSE equipment?
                  <br />
                  We look forward to helping you.
                </p>
                {renderContactUs()}
              </Col>
            </Row>
          </Container>
        );
    }
  }

  return (
    <Layout>
      <SEO
        title={intl.formatMessage({ id: 'about-seo-title' })}
        description={intl.formatMessage({ id: 'about-seo-description' })}
        keywords={intl.formatMessage({ id: 'about-seo-keyword' })}
      />

      <main role="main">
        <article>
          <header className="page-header">
            <Container>
              <Row>
                <Col>
                  <BreadCrumbs section="about-us" />
                  <h1 className="page-title">
                    <FormattedMessage id="about-aviaco" />
                  </h1>
                  <p className="page-description">
                    <FormattedMessage id="about-aviaco-description" />
                  </p>
                  <Separator />
                </Col>
              </Row>
            </Container>
          </header>
          {renderContent(intl.locale)}
        </article>
      </main>
    </Layout>
  );
};

export const query = graphql`
  {
    allFile(filter: { name: { in: ["firm", "services", "commitment"] } }) {
      edges {
        node {
          name
          childImageSharp {
            id
            fluid {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
  }
`;

export default AboutPage;
